.filter-collapse {
  margin-bottom: 20px;
  border-radius: 3px;
  box-shadow: 0 11px 21px 0 $gray-lighter;
  border: solid 1px $gray-lighter;
  background-color: $white;

  &__top {
    padding: 20px;
    border-bottom: 1px solid $gray-lighter;
  }

  &__options {
    
    .select-input {
      margin-bottom: 30px;
      border-radius: 3px;
      border: solid 1px #e3e7ee;
      background-color: #ffffff;

      &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
      }

      &__item {
        position: relative;
        padding: 5px 20px 5px 10px;
        margin: 5px 3px;
        border-radius: 3px;
        background-color: $blue;
        color: $white;

        &:last-child {
          margin-right: 0;
        }
      }

      &__close {
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(-45deg);
        right: 7px;
        font-size: 20px;
        cursor: pointer;
      }
    }
  }

  &__switch {
    display: flex;
    align-items: center;
    text-align: right;

    > span {
      display: inline-block;
      vertical-align: middle;
      margin-right: 17px;
      line-height: 70px;
    }

    .page-switch {
      vertical-align: middle;
    }

    @include media-breakpoint-up(md) {
      text-align: left;
    }
  }

  &__bottom {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .btn-cancel {
      margin-bottom: 10px;

      @include media-breakpoint-up(md) {
        margin-bottom: auto;
        margin-right: 10px;
        margin-left: auto;
      }
    }

    @include media-breakpoint-up(md) {
      flex-direction: row;
      padding-bottom: 25px;
    }
  }
}
