.user-form {
  border-radius: 3px;
  border: solid 1px $gray-lighter;
  background-color: $white;

  &__conatiner {
    padding: 15px 20px 30px 20px;

    @include media-breakpoint-up(md) {
      padding: 25px 48% 40px 30px;
    }

    @include media-breakpoint-up(lg) {
      padding-right: 58%;
    }
  }
}
